import React, { useState, useEffect } from "react";
import * as XLSX from "xlsx"; // For reading Excel files
import "./Mockup.css";
import { Form, Stack } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/helpers";
import useToast from "../../hooks/useToast";
import config from "../../utils/config.json"; // ‚úÖ Import config.json

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [operationChoice, setOperationChoice] = useState("");
  const [validationErrors, setValidationErrors] = useState([]);
  const { showErrorToast } = useToast();

  useEffect(() => {
    console.log("üîÑ Config loaded:", config);
  }, []);

  const validateExcelData = async (file) => {
    const reader = new FileReader();

    reader.onload = (e) => {
      const data = new Uint8Array(e.target.result);
      const workbook = XLSX.read(data, { type: "array" });
      const firstSheet = workbook.Sheets[workbook.SheetNames[0]];
      const jsonData = XLSX.utils.sheet_to_json(firstSheet);

      // Get mandatory fields based on operation choice
      let operationType = "";
      switch (operationChoice) {
        case "1":
          operationType = "Physical Product";
          break;
        case "2":
          operationType = "Physical Product Structure";
          break;
        case "3":
          operationType = "Document";
          break;
        case "4":
          operationType = "Physical Product-Document";
          break;
        default:
          return;
      }

      const mandatoryFields =
        config.MassUpload[operationType].mandatoryAttributes;
      const errors = [];

      // Validate each row
      jsonData.forEach((row, index) => {
        mandatoryFields.forEach((field) => {
          if (!row[field] || row[field].toString().trim() === "") {
            errors.push(`Row ${index + 2}: ${field} is required`);
          }
        });
      });

      if (errors.length > 0) {
        setValidationErrors(errors);
        setModalShow(true);
      } else {
        // Process valid data
        console.log("Excel data is valid:", jsonData);
      }
    };

    reader.readAsArrayBuffer(file);
  };

  const handleFileUpload = (files) => {
    if (files.length > 0) {
      validateExcelData(files[0]);
    }
  };

  const handleDownloadTemplate = () => {
    console.log("‚¨áÔ∏è Downloading template for operation:", operationChoice);
    downloadTemplate(showErrorToast)(operationChoice);
  };

  const handleSubmit = () => {
    if (validationErrors.length > 0) {
      showErrorToast("‚ö†Ô∏è Cannot submit! Fix errors first.");
      setModalShow(true);
      return;
    }

    console.log("‚úÖ Data is valid. Submitting...");
    // Proceed with API submission or next steps
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and download template */}
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              width: "30%",
              border: "1px solid #007bff",
              borderRadius: "8px",
            }}
            onChange={(e) => {
              console.log("üìå Selected Operation:", e.target.value);
              setOperationChoice(e.target.value);
            }}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>

          <CustomButton
            variant="link ms-auto btn-lg"
            onClick={handleDownloadTemplate}
            text="Download Template"
          />
        </Stack>

        {/* Drag and drop container */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
        />

        {/* Form-CTA submit */}
        <Stack direction="horizontal">
          <Form.Check type="checkbox" label="background" />
          <CustomButton
            variant="primary ms-auto"
            text="Submit"
            onClick={handleSubmit}
          />
        </Stack>
      </Stack>

      {/* Modal to show missing fields */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Validation Errors"
        footerButtons={[
          {
            label: "Close",
            variant: "primary",
            onClick: () => setModalShow(false),
          },
        ]}
      >
        <div>
          <h4>The following errors were found:</h4>
          <ul>
            {validationErrors.map((error, index) => (
              <li key={index} style={{ color: "red" }}>
                {error}
              </li>
            ))}
          </ul>
        </div>
      </CustomModal>
    </>
  );
};

export default MassUpload;
